(function() {
	const $tagsInput = document.querySelector('#tags_input');
	if($tagsInput) {
		// Escuchar los cambios en el input
		$tagsInput.addEventListener('keypress', guardarTag);

		function guardarTag(e) {
			if(e.keyCode === 44){
				console.log("Presionaste coma: ", e.target.value);
				e.target.value = '';
			}
		}
	}
})();

